import api from '../../api/index'
export default {
    name:'basicInformation',
    data(){
        return{
            inputName:'',
            inputSex:'',
            inputTel:'',
            inputlogin:'',
            inputPass:'',
            confirmPwd:''
        }
    },
    created(){
        this.getBaseUser()
    },
    methods:{
        // 加载初始数据
        getBaseUser(){
            api.baseUser().then(res=>{
                console.log(res)
                if(res.obj.sex == 0){
                    res.obj.sex = '男'
                }
                if(res.obj.sex == 1){
                    res.obj.sex = '女'
                }
                this.inputName = res.obj.name;
                this.inputSex = res.obj.sex;
                this.inputTel = res.obj.tel
            })
        },
        // 修改个人信息
        editorbase(){
            if(this.inputSex != '男' && this.inputSex != '女'){
                this.$message({
                    showClose: true,
                    message: '请输入男或女',
                    type: 'warning'
                })
                return;
            }
            console.log(this.inputSex)
            if(!(/^1[34578]\d{9}$/.test(this.inputTel))){
                this.$message({
                    showClose:true,
                    message:'请输入正确的手机号',
                    type: 'warning'
                })
                return;
            }
            if(this.inputSex == '男'){
                this.inputSex = 0
            }
            if(this.inputSex == '女'){
                this.inputSex = 1
            }
            let user = {
                name:this.inputName,
                sex: this.inputSex,
                tel:this.inputTel
            }
            api.editorUser(user).then(res=>{
                this.$message({
                    showClose: true,
                    message: res.msg,
                    type: 'success'
                })
            })
            this.getBaseUser();
        }
    }
}



